import { Component, OnInit } from '@angular/core'
import { ImageCroppedEvent } from 'ngx-image-cropper'
import { MatDialog, MatDialogRef } from '@angular/material'
import { WebcamImage } from 'ngx-webcam'

import { TakePhotoDialogComponent } from './take-photo-dialog/take-photo-dialog.component'

@Component({
  selector: 'app-edit-photo-card',
  templateUrl: './edit-photo-card.component.html',
  styleUrls: ['./edit-photo-card.component.css'],
})
export class EditPhotoCardComponent implements OnInit {
  files: File[] | WebcamImage[] = []
  croppedImage: any = ''
  takePhotoMode: Boolean = false

  takePhotoDialogRef: MatDialogRef<TakePhotoDialogComponent>

  takenWithWebcamImageURL: String = ''
  takenImageFile: File = null

  incorrectFileTypeProvided = false

  constructor(private dialog: MatDialog) {}

  ngOnInit() {}

  openTakePhotoDialog() {
    const dialogRef = (this.takePhotoDialogRef = this.dialog.open(
      TakePhotoDialogComponent,
      {
        width: '500px',
      }
    ))

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed with result:', result)

      fetch(result)
        .then(res => res.blob())
        .then(blob => {
          const file = new File([blob], 'File name', { type: 'image/jpeg' })
          console.log(file)
          this.takenImageFile = file
          this.files = []
        })
    })
  }

  isWebcamImage(file: File | WebcamImage): file is WebcamImage {
    return (<WebcamImage>file).imageAsDataUrl !== undefined;
  }

  filesDropped(files: File[] | WebcamImage[]): void {
    for (let file of files) {
      if (!this.isWebcamImage(file)) {
        if (!file.type.includes('image')) this.incorrectFileTypeProvided = true
        else this.incorrectFileTypeProvided = false
      }
    }
    this.files = files
    this.takenImageFile = null
  }

  onFileChanged(event): void {
    const file = event.target.files[0] as File
    this.filesDropped([file] as File[])
  }

  imageLoaded() {}

  loadImageFailed() {}

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64
  }
}



