import { Component, OnInit, Inject } from "@angular/core";
import { Subject } from "rxjs/Subject";
import { Observable } from "rxjs/Observable";
import { WebcamImage, WebcamInitError } from "ngx-webcam";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import {
  EditPhotoCardComponent,
} from "../edit-photo-card.component";

@Component({
  selector: "app-take-photo-dialog",
  templateUrl: "./take-photo-dialog.component.html",
  styleUrls: ["./take-photo-dialog.component.css"]
})
export class TakePhotoDialogComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<EditPhotoCardComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  // toggle webcam on/off
  public showWebcam = true;
  public errors: WebcamInitError[] = [];

  // latest snapshot
  public webcamImage: WebcamImage = null;
  public webcamImageAsURL: String = "";

  public webcamImageAsFile: File = null;

  // webcam snapshot trigger
  private trigger: Subject<void> = new Subject<void>();

  ngOnInit() {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  public triggerSnapshot(): void {
    this.trigger.next();
  }

  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }

  public handleImage(webcamImage: WebcamImage): void {
    console.info("received webcam image", webcamImage);
    this.webcamImage = webcamImage;

    this.webcamImageAsURL = webcamImage.imageAsDataUrl;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }
}



