import {
  Directive,
  HostBinding,
  HostListener,
  Output,
  EventEmitter,
} from '@angular/core'

@Directive({
  selector: '[appDrag]',
})
export class DragDirective {
  @Output() files: EventEmitter<File[]> = new EventEmitter()

  @HostBinding('style.background') private background = '#eee'

  constructor() {}

  @HostListener('dragover', ['$event']) public onDragOver(evt: DragEvent) {
    evt.preventDefault()
    evt.stopPropagation()
    this.background = '#999'
  }

  @HostListener('dragleave', ['$event']) public onDragLeave(evt: DragEvent) {
    evt.preventDefault()
    evt.stopPropagation()
    this.background = '#eee'
  }

  @HostListener('drop', ['$event']) public onDrop(evt: DragEvent) {
    evt.preventDefault()
    evt.stopPropagation()
    this.background = '#eee'

    let files: File[] = []
    for (let i = 0; i < evt.dataTransfer.files.length; i++) {
      const file = evt.dataTransfer.files[i] as File
      files.push(file)
    }
    if (files.length > 0) {
      this.files.emit(files)
    }
  }
}



